import { Component, OnInit } from '@angular/core';
import axios from 'axios';
import {Router} from '@angular/router';
import { api } from '../server';
import { AlertController, LoadingController } from '@ionic/angular';

@Component({
  selector: 'app-test',
  templateUrl: './test.page.html',
  styleUrls: ['./test.page.scss'],
})
export class TestPage implements OnInit {

  constructor(
    public alertCtrl: AlertController,
    public loadingCtrl: LoadingController,
    public router: Router
  ) { }

  data = {
    name: '',
    normalFrom: '',
    normalTo: '',
    price: '',
    type: 'range',
    fixedValues: ''
  }

  options = [
    {
      ageFrom: 0,
      ageTo: 0,
      normalFrom:0,
      normalTo:0
    }
  ]

  ngOnInit() {
    
  }


  addNewOption() {
    this.options.push(
      {
        ageFrom: 0,
        ageTo: 0,
        normalFrom:0,
        normalTo:0
      }
    )
  }


  test() {
    console.log(this.data.fixedValues);
    console.log();
    
  }

  async save() {
    let loading = await this.loadingCtrl.create({
      message: 'Please wait...'
    });
    await loading.present();
    let fixed = 0;
    let options: any;
    if (this.data.type == 'fixed') {
      fixed = 1;
      options = this.data.fixedValues.split('\n');
    } else {
      options = this.options;
    }
    axios.post(api + 'createTest', {
      testName: this.data.name,
      price: this.data.price,
      fixed: fixed,
      options: options
    }).then(async res => {
      let message = await this.alertCtrl.create({
        header: 'Test has been added Successfully.',
        subHeader: 'Do you want to add a new test?',
        buttons: [
          {
            text: 'Yes',
            handler: () => {
              this.data = {
                name: '',
                normalFrom: '',
                normalTo: '',
                price: '',
                type: 'range',
                fixedValues: ''
              }
            
              this.options = [
                {
                  ageFrom: 0,
                  ageTo: 0,
                  normalFrom:0,
                  normalTo:0
                }
              ]
            }
          },
          {
            text: 'No',
            handler: () => {
              this.router.navigateByUrl('/tests')
            }
          }
        ]
      });
      await message.present();
      
    }).finally(async () => {
      await loading.dismiss()
    })
  }

  segmentChanged(e) {
    return e
  }
}
